import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { JamKerjaRoutingModule } from './jam-kerja-routing.module';
import { ListComponent } from './list/list.component';
import { EditComponent } from './edit/edit.component';
import { AddComponent } from './add/add.component';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { NzRadioModule, NgZorroAntdModule } from 'ng-zorro-antd';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';



@NgModule({
    declarations: [ListComponent, EditComponent, AddComponent],
    imports: [
        CommonModule,
        JamKerjaRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        NzRadioModule,
    ],
    providers: [FormBuilderTypeSafe],
})
export class JamKerjaModule { }
