import { Jadwal_tipe } from 'src/app/sdk/hr/models/jadwal-_tipe';
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { AbsensiJadwalTipeService, AbsensiJadwalTipeNormalDetailService } from 'src/app/sdk/hr/services';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { Jadwal_tipe_normal_detail } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private spinner: NgxSpinnerService,
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private absensiJadwalTipeNormalDetailService: AbsensiJadwalTipeNormalDetailService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router,
    ) { }

    id_jadwal_tipe: string = ''
    ngOnInit() {
        this.id_jadwal_tipe = this.activatedRoute.snapshot.paramMap.get('id_jadwal_tipe')
        this.form();
        this.getData()
        this.getItem()
    }
    listJadwakType: any[] = []
    numberToHari = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
    getItem() {
        let filter = {
            id_jadwal_tipe: this.id_jadwal_tipe,
        }
        this.absensiJadwalTipeNormalDetailService.getAbsensiJadwalTipeNormalDetails(JSON.stringify(filter)).subscribe(
            data => {

                let out = [];
                data.data.forEach(item => {
                    let param: any = item;
                    param['nama_hari'] = this.numberToHari[parseInt(item.hari) - 1];

                    if (param.status_jadwal == 'ya') {
                        param.status_jadwal = true;
                    } else (
                        param.status_jadwal = false
                    )
                    out.push(param);
                })

                this.listJadwakType = out;

            }
        )
    }

    // event update
    updateDetailJadwal(id_jadwal_tipe_detail: string, data: any) {
        if (data.status_jadwal == true) {
            data.status_jadwal = 'ya';
        } else {
            data.status_jadwal = 'tidak'
        }
        let param: Jadwal_tipe_normal_detail = {
            jam_masuk: data.jam_masuk,
            jam_keluar: data.jam_keluar,
            lama_dikantor: data.lama_dikantor,
            toleransi_telat: data.toleransi_telat,
            toleransi_cabut: data.toleransi_cabut,
            jam_istirahat: data.jam_istirahat,
            status_jadwal: data.status_jadwal
        }

        this.spinner.show()
        this.absensiJadwalTipeNormalDetailService.putAbsensiJadwalTipeNormalDetailsId({
            id: id_jadwal_tipe_detail,
            body: param
        }).subscribe(
            data => {
                this.spinner.hide();
                this.getItem()
            }
        )
    }


    getData() {
        this.absensiJadwalTipeService.getAbsensiJadwalTipesId(this.id_jadwal_tipe).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Jadwal_tipe>
    form() {
        this.dataForm = this.fb.group<Jadwal_tipe>({
            id_jadwal_tipe: new FormControl('', [Validators.required]),
            tipe: new FormControl('', [Validators.required]),
            jenis: new FormControl('', [Validators.required]),
            validasi: new FormControl(''),
            keterangan: new FormControl(''),
            is_holiday: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }

    jadwalTipe: Jadwal_tipe = {}
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.jadwalTipe = this.dataForm.value
        this.absensiJadwalTipeService
            .putAbsensiJadwalTipesId({
                id: this.id_jadwal_tipe,
                body: this.jadwalTipe,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/jam-kerja/list'])
    }

}
